* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  padding: 0;
	box-sizing: border-box;
}

body {
  margin: 0;
  /* max-width: fit-content; */
  width: 100%;
}

div {
  display: block;
}

html {
  scroll-behavior: smooth;
}

label{
  color: #22bc9c;
  font-size: 18px;
  font-weight: bold;
}

.jumbotron {
  margin:0;
  padding: 50px 100px;
  background: linear-gradient(360deg,#444444 10%,#4a4a4a 360%);
}

.title {
  margin-bottom: 80px;
  display: block;
  text-align: center;
}

.skills {
  margin-top: 150px;
}
@media (max-width: 700px) {
  .jumbotron {
    margin:0;
    padding: 20px 20px;
    background: linear-gradient(360deg,#444444 10%,#4a4a4a 360%);
  }
  #imgjs{
    display: none;
  }

  .title {
    margin-bottom: 10px;
    display: block;
    text-align: center;
  }
  
  .skills{
    margin-top: 5px !important;
  }
}

.profile-image img {
  width: 450px;
  height: 200px;
  border-radius: 120px;
  margin: auto;
  display: block;
}

.text {
  margin-top: 20px;
  display: block;
  text-align: center;
  margin: auto;
  color: #22bc9c;
  text-shadow: 2px 1px #309c87;
}

.navbar-brand {
  padding: 0 90px;
}

#navbarColor01 {
  padding: 0 90px;
}


.display-3 {
  font-weight: 400;
  color: #22bc9c;
}

.lead {
  font-weight: 500;
}

.display-3 {
  color: #22bc9c;
  font-weight: 400;
}

.info-section {
  background-color: #1a1a1a;;
  color: white;
  padding: 80px 50px 50px 50px;
  border-radius: 50px;
  font-weight: 50%;
  font-size: 25px;
  text-align: justify;
}

@media (max-width: 700px) {
  .info-section {
    padding: 40px 30px 30px 30px;
    margin: 5px 0 ;
  }
}

.blockquote {
  font-size: 25px;
}


.css {
  width: 70%;
  float: right;
}

.js , .react{
  width: 80%;
  float: right;
}

.html , .mysql{
  width: 100%;
  float: right;
}

.content{
  color: #22bc9c;
  font-size: large;
}


.card{
  max-width: 30rem;
}

.info-color-dark{
  text-align: center;
  color: #22bc9c;
  font-size: 1.2rem;
}

.vermas{
  text-align: center;
  display: inline-block;
}

.icon{
  color: #22bc9c;
}

.icon:hover{
  color: #2becc6;
}

@media (max-width: 200px) {
  .jumbotron {
    margin: 0;
    padding: 50px 50px;
    border-radius: 0;
  }
  .navbar-brand {
    padding: 0 5px;
  }
}

@media (max-width: 765px) {
  .navbar-brand {
    padding: 0 5px;
  }
}

.message {
  display: block;
  margin: 0 auto;
}

.wrap {
  background-size: auto;
  background-repeat: repeat;
  width: fit-content;
  /* max-width: fit-content; */
  background-position: center;
  background-attachment: fixed;
}

@media screen and (device-width: 360px) and (device-height: 640px) and (-webkit-device-pixel-ratio: 3)	{
  .wrap {
    width: min-content;
    background-position: center;
    max-width: fit-content !important;
  }
  .navbar-brand {
    padding: 0 5px;
  }
  .main-info-section {
  background-size: cover;
  }
}

@media (max-width: 1200px) {
  .jumbotron {
    margin:0;
    padding: 20px 20px;
    background: linear-gradient(360deg,#444444 10%,#4a4a4a 360%);
  }

  .title {
    margin-bottom: 10px;
    display: block;
    text-align: center;
  }
  
  .skills{
    margin-top: 5px !important;
  }

  .main-info-section {
    background-size: cov  er;
  }
}

.main-info-section {
  background-image: url("https://images.hdqwalls.com/download/beautiful-blue-purple-sky-4k-9g-2560x1440.jpg");
  background-repeat: no-repeat;
  background-size: 1950px 1000px;
  min-height: 100vh;
  cursor: default;
  background-attachment: fixed;
  background-position: center center;
}

.btn-green {
  color: white;
  background-color:  #00b490;
}

.btn-green:hover {
  color: #444444;
  background-color:  #2addb9;
}

#about .title{
  margin-bottom: 100px;
}

#skill .title{
  margin-bottom: 20px;
}

.advice {
  background-color: #2c2c2c;
  width: none;
}

@media screen and (min-width: 1440px) and (max-width: 2000px) {
  .contactimg {
    height: 500px;
    width: 700px;
    margin: 10px 10px;
  } 
}


