/* Laptop */
@media screen and (min-width: 1024px) and (max-width: 1440px) {
  .info-section {
    padding: 40px 30px 20px 30px;
    font-size: 18px;  
  }
  #about .row .img-fluid {
    margin-top: 20px;
  }
  .blockquote {
    font-size: 20px;
  }
}

/* FULL  4K*/
@media screen and (min-width: 2000px) and (max-width: 2560px) {
  .info-section {
    width: 2560px;
  }
  .about {
    width: 2560px;
  }
  .main-info-section {
    background-repeat: no-repeat;
    background-size: 2730px 1000px;
    min-height: 100vh;
  }
  .info-section {
    padding: 100px 60px 60px 60px;
    font-size: 35px;  
  }
  #about .row .img-fluid {
    margin-top: 20px;
  }
  .blockquote {
    font-size: 35px;
  }
  .skills {
    margin-top: 300px;
  }
}

@media screen and (min-width: 320px) and (max-width: 438px) {
  .content {
    width: fit-content;
  }
  .contactimg {
    height: 200px;
    width:400px;
    margin: 30px 20px;
  }
}